/* src/components/Home.css */
.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh; /* Full viewport height */
  background-color: #1e1e1e; /* Dark background for coding theme */
  color: #dcdcdc; /* Light text color for readability */
  padding: 20px;
  box-sizing: border-box;
  font-family: 'Fira Code', monospace; /* Monospaced font for coding theme */
}

.title {
  font-size: 3.5rem; /* Responsive font size */
  margin: 0 0 20px;
  color: #ffffff; /* Light cyan for the title */
}

.intro {
  font-size: 1.2rem; /* Slightly larger font size for paragraphs */
  margin: 10px 0;
  max-width: 800px; /* Maximum width for readability */
}

.highlight {
  color: rgba(255, 0, 0, 0.714); /* Syntax highlighting color */
  font-weight: bold;
}

.call-to-action {
  font-size: 1.2rem;
  margin-top: 20px;
}

.resume-link {
  color: #a6e22e; /* Stylish link color */
  text-decoration: none;
  font-weight: bold;
}

.resume-link:hover {
  text-decoration: underline; /* Underline on hover */
}

/* Image gallery styles */
.image-gallery {
  display: flex;
  flex-wrap: wrap; /* Wrap images on smaller screens */
  justify-content: center;
  align-items: center;
  gap: 20px; /* Space between images */
  margin-top: 30px;
}

.image-gallery img {
  width: 100%; /* Responsive width */
  max-width: 300px; /* Fixed maximum width for images */
  height: auto; /* Maintain aspect ratio */
  object-fit: cover; /* Maintain aspect ratio and cover the area */
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow */
}

/* Responsive design adjustments */
@media (max-width: 1024px) {
  .home {
    padding: 30px; /* Adjust padding for tablets */
  }

  .title {
    font-size: 2.2rem; /* Adjust font size for tablets */
  }

  .intro {
    font-size: 1.1rem; /* Adjust font size for tablets */
  }

  .call-to-action {
    font-size: 1rem; /* Adjust font size for tablets */
  }

  .image-gallery {
    gap: 15px; /* Adjust gap for tablets */
  }
}

@media (max-width: 768px) {
  .home {
    padding: 20px; /* Adjust padding for mobile screens */
  }

  .title {
    font-size: 1.8rem; /* Adjust font size for mobile screens */
  }

  .intro {
    font-size: 1rem; /* Adjust font size for mobile screens */
  }

  .call-to-action {
    font-size: 0.9rem; /* Adjust font size for mobile screens */
  }

  .image-gallery {
    flex-direction: column; /* Stack images vertically on small screens */
    align-items: center;
  }

  .image-gallery img {
    max-width: 100%; /* Ensure images use full width on small screens */
  }
}

@media (max-width: 480px) {
  .title {
    font-size: 1.5rem; /* Further adjust font size for very small screens */
  }

  .intro {
    font-size: 0.9rem; /* Further adjust font size for very small screens */
  }

  .call-to-action {
    font-size: 0.75rem; /* Further adjust font size for very small screens */
  }

  .resume-link {
    font-size: 0.85rem; /* Adjust link font size for very small screens */
  }
}
