/* src/components/Terminal.css */
.terminal {
  background-color: #000;
  color: #00ff00;
  font-family: 'Fira Code', monospace;
  border-radius: 5px;
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.3);
  overflow-y: auto;
  height: 300px;
  position: relative;
}

/* Terminal output styles */
.terminal-output {
  white-space: pre-wrap;
  margin-bottom: 10px;
}

/* Terminal line styles */
.terminal-line {
  margin: 0;
}

/* Terminal input styles */
.terminal-input {
  background-color: #000;
  color: #00ff00;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  font-family: 'Fira Code', monospace;
  font-size: 1rem;
  resize: none;
}

/* Responsive design adjustments */
@media (max-width: 1024px) {
  .terminal {
    padding: 15px; /* Adjust padding for tablets */
    height: 250px; /* Adjust height for tablets */
    font-size: 0.95rem; /* Slightly reduce font size */
  }
}

@media (max-width: 768px) {
  .terminal {
    padding: 10px; /* Adjust padding for mobile screens */
    height: 200px; /* Reduce height for mobile screens */
    font-size: 0.9rem; /* Further reduce font size for readability */
  }

  .terminal-input {
    font-size: 0.9rem; /* Adjust input font size for mobile screens */
  }
}

@media (max-width: 480px) {
  .terminal {
    padding: 8px; /* Further adjust padding for very small screens */
    height: 150px; /* Further reduce height for very small screens */
    font-size: 0.85rem; /* Further reduce font size */
  }

  .terminal-input {
    font-size: 0.85rem; /* Adjust input font size for very small screens */
  }
}
