/* src/components/Resume.css */
.resume {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(135deg, #d4d4d4, #c3cfe2); /* Gradient background */
  border-radius: 15px;
  box-sizing: border-box;
  width: 100%; /* Ensure it takes full width */
}

.resume h1 {
  color: #2c3e50;
  font-size: 2rem;
  margin-bottom: 20px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1); /* Subtle text shadow */
  text-align: center; /* Center text on all screens */
}

.pdf-container {
  height: 60vh; /* Set a specific height to make it scrollable */
  overflow-y: auto; /* Make the content scrollable if it overflows */
  border: 1px solid #e0e0e0; /* Light border */
  border-radius: 15px; /* Rounded corners */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Larger shadow effect */
  background-color: #ffffff; /* White background for the container */
  transition: box-shadow 0.3s ease, transform 0.3s ease; /* Smooth transition */
}

.pdf-container:hover {
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.3); /* Deeper shadow on hover */
  transform: scale(1.02); /* Slight zoom effect on hover */
}

.pdf-viewer {
  border-radius: 10px; /* Rounded corners for the PDF viewer */
}

.download-link {
  display: inline-block;
  padding: 10px 20px;
  margin-top: 20px;
  color: #ffffff;
  background-color: #3498db;
  text-decoration: none;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Shadow for the button */
  transition: background-color 0.3s ease, box-shadow 0.3s ease; /* Smooth transition */
}

.download-link:hover {
  background-color: #2980b9;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Deeper shadow on hover */
}
