/* src/components/Navbar.css */
.navbar {
    background-color: #333;
    display: flex;
    justify-content: center; /* Center the navbar items */
    align-items: center;
    padding: 15px 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
    position: sticky; /* Sticky navbar */
    top: 0;
    width: 100%;
    z-index: 1000; /* Ensure navbar is above other elements */
  }
  
  .nav-link {
    color: #ffffff;
    text-decoration: none;
    margin: 0 15px;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    transition: color 0.3s ease, transform 0.3s ease; /* Smooth transition */
  }
  
  .nav-link:hover {
    color: #3498db; /* Hover color change */
    transform: scale(1.1); /* Slight zoom effect on hover */
  }
  
  .nav-icon {
    margin-right: 8px;
    font-size: 1.5rem;
  }
  
  /* Responsive Design Adjustments */
  
  /* For tablets and smaller screens */
  @media (max-width: 768px) {
    .navbar {
      flex-direction: column; /* Stack links vertically on smaller screens */
      padding: 10px 0;
    }
  
    .nav-link {
      margin: 10px 0; /* Add margin between stacked links */
      font-size: 1rem; /* Adjust font size for smaller screens */
    }
  
    .nav-icon {
      font-size: 1.2rem; /* Adjust icon size for smaller screens */
    }
  }
  
  /* For very small mobile devices */
  @media (max-width: 480px) {
    .nav-link {
      font-size: 0.9rem; /* Further adjust font size for very small screens */
    }
  
    .nav-icon {
      font-size: 1rem; /* Further adjust icon size */
    }
  }
  