body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f0f2f5;
    color: #333;
  }
  
  .app {
    max-width: 900px;
    margin: auto;
    padding: 20px;
    background: #ffffff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
  }
    
  .home, .resume {
    text-align: center;
    padding: 50px;
    box-sizing: border-box; /* Ensure padding is included in element's total width and height */
  }
  
  .home p, .resume h1 {
    margin: 0;
  }
  
  /* Responsive styles */
  
  /* For tablets and smaller screens */
  @media (max-width: 1024px) {
    .app {
      padding: 15px; /* Adjust padding for tablets */
    }
  
    .navbar {
      flex-direction: column;
      padding: 10px;
    }
  
    .nav-link {
      margin: 10px 0; /* Stack links vertically */
      font-size: 16px; /* Adjust font size */
    }
  
    .home, .resume {
      padding: 40px; /* Adjust padding for tablets */
    }
  
    .home p {
      font-size: 1.1rem; /* Adjust font size for paragraphs */
    }
  
    .resume h1 {
      font-size: 2rem; /* Adjust font size for headings */
    }
  
    .pdf-container, .pdf-viewer {
      max-width: 100%; /* Ensure they fit within the container */
    }
  }
  
  /* For mobile devices */
  @media (max-width: 768px) {
    .app {
      padding: 10px; /* Adjust padding for mobile screens */
    }
  
    .navbar {
      flex-direction: column;
      padding: 10px;
    }
  
    .nav-link {
      margin: 10px 0; /* Stack links vertically */
      font-size: 14px; /* Adjust font size */
    }
  
    .home, .resume {
      padding: 30px; /* Adjust padding for mobile screens */
    }
  
    .home p {
      font-size: 1rem; /* Adjust font size for paragraphs */
    }
  
    .resume h1 {
      font-size: 1.8rem; /* Adjust font size for headings */
    }
  
    .pdf-container {
      width: 100%; /* Ensure it fits the width of the container */
    }
  
    .pdf-viewer {
      width: 100%; /* Ensure it fits the width of the container */
    }
  
    .download-link {
      padding: 8px 16px; /* Adjust button size for mobile screens */
      font-size: 0.9rem; /* Adjust font size */
    }
  }
  
  /* For very small mobile devices */
  @media (max-width: 480px) {
    .home p {
      font-size: 0.9rem; /* Further adjust font size for very small screens */
    }
  
    .resume h1 {
      font-size: 1.5rem; /* Further adjust font size for very small screens */
    }
  
    .download-link {
      padding: 6px 12px; /* Further adjust button size for very small screens */
      font-size: 0.8rem; /* Further adjust font size */
    }
  }
  